.panel {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  height: 20vh;

  button {
    font-size: 1.2rem;
    margin: 1rem;
    padding: 1rem 2rem;
    background-color: $color-dark-blue;
    border: none;
    color: $color-white;
    cursor: pointer;
    border-radius: 1rem;
  }

  p {
    font-size: 1.2rem;
  }

  .library-panel,
  .generate-panel,
  .save-panel {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
